@font-face {
  font-family: "Open Sans";
  src: url("../../fontsP2/OpenSans-Light.eot");
  src: url("../../fontsP2/OpenSans-Light.eot?#iefix")
      format("embedded-opentype"),
    url("../../fontsP2/OpenSans-Light.woff2") format("woff2"),
    url("../../fontsP2/OpenSans-Light.woff") format("woff"),
    url("../../fontsP2/OpenSans-Light.ttf") format("truetype"),
    url("../../fontsP2/OpenSans-Light.svg#OpenSans-Light") format("svg");
  font-weight: 300;
  font-style: normal;
}

